<template>
  <el-menu
    active-text-color="#008997"
    background-color="#1E2337"
    class="el-menu-vertical-demo"
    text-color="#fff"
    router
    :default-active="activeIndex"
  >
    <el-menu-item index="/home">
      <el-icon><home-filled /></el-icon>
      <span @click="homeTab()">首页</span>
    </el-menu-item>

    <el-sub-menu :index="menu.path" v-for="menu in menuList">
      <template #title>
        <component
          :is="menu.icon"
          style="width: 16px; height: 16px; margin-right: 6px"
        ></component>
        <span>{{ menu.name }}</span>
      </template>
      <el-menu-item
        :index="item.path"
        v-for="item in menu.children"
        @click="openTab(item)"
      >
        <component
          :is="item.icon"
          style="width: 16px; height: 16px; margin-right: 6px"
        ></component>
        <span>{{ item.name }}</span>
      </el-menu-item>
    </el-sub-menu>
  </el-menu>
</template>

<script setup>
import { ref, watch } from "vue";
import requestUtil from "@/util/request";
import store from "@/store";

let menuList = ref(store.getters.GET_MENULIST);
const activeIndex = ref("/index");

async function getMenu() {
  var menu;
  menu = store.getters.GET_MENULIST;
  if (menu == null) {
    let result = await requestUtil.get("admin_api/v1/menus");
    let data = result.data.data;
    menu = data;
    store.commit("SET_MENULIST", menu);
  }
  menuList.value = menu;
}
const openTab = (item) => {
  store.commit("ADD_TABS", item);
};
watch(
  store.state,
  () => {
    activeIndex.value = store.state.editableTabsValue;
  },
  { deep: true, immediate: true }
);
const homeTab = () => {
  store.commit("ADD_TABS", {
    name: "首页",
    path: "/home",
  });
};

if (menuList.value == null) {
  getMenu();
}
</script>

<style scoped></style>
